.item {
  margin: 0 0 0 20px;
  width: 100%;
  font-weight: bold;
  font-size: 20px;
}
.content_feedback {
  width: 120px;
  margin: 12px 0;
  display: flex;
  justify-content: space-between;
}

.content_feedback span {
  margin: 5px;
  font-size: 20px;
  font-weight: bold;
}
.avatar img {
  width: 80px;
  border-radius: 50%;
}
.content__user {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #f7f6f4cc;
  border: 3px solid #fff;
  border-radius: 10px;
}
